@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 400;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

html {
  font-family: Avenir, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-form {
  margin-top: 20px;
}

.search-form__input {
  color: #3d4e61;
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: 0;
  width: 100%;
  height: 70px;
  padding-left: 62px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  transition: box-shadow .1s linear;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-form__input::placeholder {
  color: inherit;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search-form__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.search-form__input:focus {
  background-image: linear-gradient(#fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
}

.search-form__input--small {
  background-position: 13px;
  background-size: 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-form__label {
  display: block;
  position: relative;
}

.search-form__label:before {
  content: "";
  background: url("Search.ccd3c07b.svg") no-repeat;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 50%;
  left: 26px;
  transform: translateY(-50%);
}

.search-form__label--small:before {
  background-size: contain;
  width: 11px;
  height: 11px;
  left: 13px;
}

